.Dossier{position:relative;margin:10px;background-color:#000;box-shadow:0 2px 5px rgba(0,0,0,.5)}.Dossier .deplacer{margin-left:10px;margin-top:10px;box-sizing:border-box;position:absolute;background-color:rgba(0,0,0,.3);color:rgba(255,255,255,.5);border-radius:100%;padding:5px;mix-blend-mode:luminosity}.Dossier .deplacer>*{display:block;margin:auto}.Dossier .info{padding:0 20px}.Dossier .info>*:nth-child(1){font-weight:400}.Dossier .info>*:nth-child(2){font-size:1.1em;font-weight:600}.Dossier .info>*:nth-child(2):before{content:"Modifié : "}.Dossier button{cursor:pointer;background-color:transparent;border:none;display:inline-block;margin-right:0;margin-left:100%;transform:translate(calc(-100% - 10px), -20px);color:#fff}